
<template>
  <div>
    <Home></Home>
    <form class="form-inline">
      <div class="form-group">
        <label for="exampleInputName2">名字</label>
        <input type="text" class="form-control" v-model="hero.name" id="exampleInputName2" placeholder="英雄的名字">
      </div>
      <div class="form-group">
        <label for="exampleInputEmail2">定位</label>
        <input type="text" class="form-control" v-model="hero.profession" id="exampleInputEmail2" placeholder="英雄的定位">
      </div>
      <div class="form-group">
        <label for="exampleInputEmail2">故事</label>
        <textarea type="text" class="form-control" v-model="hero.story" placeholder="英雄的故事"></textarea>
      </div>
      <button type="submit" class="btn btn-default" v-on:click.prevent="addOne">{{hero.text}}</button>
    </form>
  </div>
</template>
<script>
import Home from "../Home"
import axios from "axios"
import router from "../../router"
 const Add = {
    components: {
      Home
    },
    data() {
      return {
        hero: {
          name: '',
          profession: '',
          story:'',
          text: '添加'
        }
      }
    },
    methods: {
      addOne() {
        //添加
        if (this.hero.text === "添加") {
          axios.post('http://127.0.0.1:8888/add', this.hero)
            .then((res) => {
              router.push('/list')
            })
        //修改
        }else{
          axios.patch('http://127.0.0.1:8888/add', this.hero)
          .then((res)=> router.push('/list'))
        }

        return false
      }
    },
    created() {
      const id = this.$route.params.id
      console.log(id)
      id && axios.get('http://127.0.0.1:8888/list/' + id)
        .then((res) => {
          console.log(res)
          this.hero = res.data[0]
          this.hero.text = '保存'
        })
      // !id ? this.hero : this.hero = {
      //   name: 'aaa',
      //   profession: '战士',
      //   id: 1
      // }
    }
  }
   export default  Add
</script>



