
<template>
<div>
  <Home></Home>
  <table class="table table-striped">
    <tr>
      <th>序号</th>
      <th>名字</th>
      <th>定位</th>
      <th>操作</th>
    </tr>
    <tr v-for="(v,i) in heros">
      <td class="active">{{i+1}}</td>
      <td class="success">{{v.name}}</td>
      <td class="warning">{{v.profession}}</td>
      <td>
              <router-link :to="'/show/' + v._id">查看</router-link>
              <router-link :to="'/add/' + v._id">编辑</router-link>
              <a @click.prevent="remove(v._id)" href="#">删除</a>
            </td>
    </tr>
  </table>
</div>
</template>
<script>
import axios from "axios"
import Home from "./Home"
import '../../node_modules/bootstrap/dist/css/bootstrap.css'
  const List = {
    components: {
      Home
    },
    data() {
      return {
        heros: []
      }
    },
    methods: {
      getHeros() {
        axios.get('http://127.0.0.1:8888/list')
          .then(function (res) {
            // console.log(res.data)
            this.heros = res.data
          }.bind(this))
      },
      remove(id) {
        if (!window.confirm('确定要删除吗？')) return
        axios.delete('http://127.0.0.1:8888/list/' + id)
          .then((res) => {
            res.status == 200 ? this.getHeros() : alert('删除失败')

          })
      }
    },
    created() {
      this.getHeros()
    }
  }
  export default List
</script>
<style lang="">
  th,td{
    text-align:left;
  }
</style>


