
<template>
<div>
    <Home></Home>
    <p>英雄名称： {{ hero.name }}</p>
    <p>英雄定位： {{ hero.profession }}</p>
    <p>背景故事： {{ hero.story }}</p>
</div>
</template>
<script>
import Home from "../Home"
import axios from "axios"
    const Show = {
    components: {
        Home
    },
    data() {
        return {
            hero: {
                name: "aaa",
                profession: '战士',
                story: '12312313'
            }
        }
    },
    created() {
        axios.get('http://127.0.0.1:8888/list/' + this.$route.params.id)
            .then((res) => this.hero = res.data[0])
    }
}
export default Show
</script>



